



































































































































































.custom-tree {
    /deep/ .ant-menu-item {
      &:hover {
        .btn-action {
          display: block;
        }
      }
    }
    /* @TODO: 这里调查为什么没有出现的问题，或许要重新做这个组件*/
    /deep/ .ant-menu-submenu-title {
      position: relative;
      &:hover {
        .btn-action {
          display: block;
        }
      }
    }
    /deep/ .btn-actions {
      display: inline-flex;
      float: right;
      clear: left;
    }
    /deep/ .btn-action {
      display: none;
      width: 20px;
      height: 40px;
      line-height: 40px;
      z-index: 1080;
      &:hover {
        transform: scale(1.2);
        transition: 0.5s all;
      }
    }
  }


