.ant-card {
  /deep/ .ant-card-head {
    min-height: 32px;
    padding: 0px 8px;
    .ant-card-head-title {
      padding: 12px 8px;
    }
  }
  /deep/ .ant-card-body {
    padding: 0px;
    border-top: 1px solid #e8e8e8;
  }
  /deep/ .ant-table {
    border: none;
    .ant-table-content {
      border-right: 0px;
    }
  }
  /deep/ .ant-card-extra {
    padding: 0px;
  }
  /deep/ .ant-tabs-tab {
    padding-top: 12px;
    padding-bottom: 12px;
    font-weight: 500;
  }
}

